import React, {Component} from 'react';
import store from '../store';
import {connect} from 'react-redux';
// import UserBoards from '../components/UserBoards';
import NoteWrapper from '../components/NoteWrapper';
import {getBoardNotes, getBoard} from '../actions/board';
import {getAllMyBoardNotes} from '../actions/note';

class UserBoardsContainer extends Component {

  componentDidMount() {
    this.props.onBoardEnter();

  }

  render() {
    console.log('props', this.props);
    const boardId = this.props.board.id;
    if (Object.keys(this.props.notes).length) {
      debugger;
    }
    console.log('notes by id', this.props.notes['board' + boardId]);
    console.log('-------------------------');
    return (
      // <Board {...this.props} />
      <div style={{
        width   : 1000,
        height  : 1000,
        position: 'relative'}}>
        { Object.keys(this.props.notes).length ? (
            this.props.notes[this.props.board.id].map((note) => {
              const { left, top } = note;

              return (
                <NoteWrapper
                  key={note.id}
                  id={note.id}
                  left={left}
                  top={top}
                  note={note}
                />
              );
            })
          ) : null
        }
      </div>
    );
  }
}


const mapStateToProps = (state, ownProps) => {
  return {
    notes: state.noteReducer.allBoardSpecificNotes,
    board: ownProps.board
  };
};

const mapDispatchToProps = (dispatch, ownProps) => (
  {
    onBoardEnter: function() {
      const boardId = ownProps.board.id;
      // dispatch(getBoard(boardId));
      dispatch(getAllMyBoardNotes({boardId}));
    }
  });

export default connect(mapStateToProps, mapDispatchToProps)(UserBoardsContainer);



